<template>
  <a-layout class="container">
    <!--  -->
    <div class="title"><span>  </span></div>
    <a-divider class="divider"></a-divider>

    <!--  -->
    <!-- 
       :sort="false" 
       clone  id
    -->
    <draggable
      v-model="componentList"
      item-key="id"
      :group="{ name: 'component', pull: 'clone', put: false }"
      :sort="false"
      :clone="(original) => deepClone(original)"
      animation="200"
    >
      <template #item="{ element }">
        <a-button class="item">{{ element.options.cType }} </a-button>
      </template>
    </draggable>

    <!--  -->
    <!-- {{ componentList }} -->
  </a-layout>
</template>

<script setup>
import draggable from "vuedraggable";
import { nanoid } from "nanoid";  
import dayjs from 'dayjs';

/*
  schema         
  field          
  name           
  refill         
  options.type   
  options.cType  
  options.value  
*/
const componentList = [
  // 
  {
    id: 1,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "input",
      cType: "",
      value: "",
      allowClear: true,
      bordered: true,
      placeholder: '',
      showCount: true,
    }
  },
  // 
  {
    id: 2,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "textarea",
      cType: "",
      value: "",
      allowClear: true,
      autoSize: {minRows: 2},
      placeholder: "",
      showCount: false,
    }
  },
  // 
  {
    id: 3,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "radio",
      cType: "",
      value: "",
      valueData: [
        {value: '1', label: '1'},
        {value: '2', label: '2'}
      ],
    }
  },
  // 
  {
    id: 4,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "select",
      cType: "",
      value: "",
      valueData: [
        { value: '1'},
        { value: '2'}
      ],
    }
  },
  // 
  {
    id: 5,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "rate",
      cType: "",
      value: 1,
      limit: 10,
    }
  },
  // 
  {
    id: 6,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "datePicker",
      cType: "",
      value: dayjs('2020-01-01').format("YYYY-MM-DD"),
    }
  },
  // 
  {
    id: 7,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "text",
      cType: "",
      value: "",
      fontFamily: "Georgia, 'Times New Roman', Times, serif",
      fontSize: "16",
      textType: ""
    }
  },
  // 
  {
    id: 8,
    schema: "",
    field: "",
    name: "",
    refill: false,
    options: {
      type: "divider",
      cType: "",
      value: "",
      style: "border-color: #63b4ff; font-family:  Georgia, 'Times New Roman', Times, serif;",
      dashed: true,
      orientation: "",
    }
  },
  // 
  // {

  // }
];

// id
function deepClone(original) {
  // 
  let newItem = JSON.parse(JSON.stringify(original));
  //  id
  newItem.id = nanoid();
  return newItem;
}

</script>

<style scoped>
.container {
  height: 100%;
  background-color: white;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.divider {
  margin-top: 0;
  border-color: #1890ff;
}

.item {
  font-size: 16px;
  font-family: 'Courier New', Courier, monospace;
  color: white;
  background-color: #1890ff;
  border-radius: 6px;
  width: 180px;
  margin: 4px;
}
</style>