<template>
  <a-layout class="container">
    <!--  -->
    <div class="title"><span>  </span></div>
    <a-divider class="divider"></a-divider>
    
    <a-form layout="vertical">
      <a-form-item
        label=""
        class="attribute"
      >
        <a-input
          v-model:value="selectedComponent.component.options.cType"
          :disabled=true
        >
        </a-input>
      </a-form-item>
      <!--  -->
      <template v-if="selectedComponent.component.options.type === 'text'">
        <a-form-item
          label=""
          class="attribute"
        >
          <a-textarea 
            v-model:value="selectedComponent.component.options.value"
            :autoSize="{minRows: 2}"
            :allowClear="true"
          >
          </a-textarea>
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-input-number 
            v-model:value="selectedComponent.component.options.fontSize" 
            :min="1" 
            :max="20" 
          />
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.textType">
            <a-radio :style="radioStyle" value=""></a-radio>
            <a-radio :style="radioStyle" value="secondary"></a-radio>
            <a-radio :style="radioStyle" value="success"></a-radio>
            <a-radio :style="radioStyle" value="warning"></a-radio>
            <a-radio :style="radioStyle" value="danger"></a-radio>
          </a-radio-group>
        </a-form-item>
      </template>
      <!--  -->
      <template v-else-if="selectedComponent.component.options.type === 'divider'">
        <a-form-item
          label=""
          class="attribute"
        >
          <a-textarea 
            v-model:value="selectedComponent.component.options.value"
            :autoSize="{minRows: 2}"
            :allowClear="true"
          >
          </a-textarea>
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.orientation">
            <a-radio :style="radioStyle" value="left"></a-radio>
            <a-radio :style="radioStyle" value=""></a-radio>
            <a-radio :style="radioStyle" value="right"></a-radio>
          </a-radio-group>
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-radio-group v-model:value="selectedComponent.component.options.dashed">
            <a-radio :style="radioStyle" :value="true"></a-radio>
            <a-radio :style="radioStyle" :value="false"></a-radio>
          </a-radio-group>
        </a-form-item>
      </template>
      <!--  -->
      <template v-else>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-input
            v-model:value="selectedComponent.component.name"
          >
          </a-input>
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-select
            v-model:value="selectedComponent.component.schema"
            :options="schemas"
            @change="() => fieldClear()"
          ></a-select>
        </a-form-item>
        <a-form-item
          label=""
          class="attribute"
        >
          <a-select
            v-model:value="selectedComponent.component.field"
            :options="fields"
          ></a-select>
        </a-form-item>
        <!-- ,  -->
        <a-form-item
          v-if="selectedComponent.component.options.type === 'input'"
          label=""
          class="attribute"
        >
          <a-switch v-model:checked="selectedComponent.component.refill" />
          <exclamation-circle-two-tone
            title=""
            style="margin-left: 6px;"
          />        
        </a-form-item>
        <!-- ,  -->
        <a-form-item 
          v-if="selectedComponent.component.options.type === 'radio' || selectedComponent.component.options.type === 'select'"
          label=""
          class="attribute"
        >
          <a-button @click="radioAdd" size="small" style="margin-bottom: 4px;">
            
          </a-button>
          <template v-for="(item, index) in selectedComponent.component.options.valueData" :key="index">
            <a-input v-model:value="item.value">
              <template #addonAfter>
                <minus-circle-outlined @click="() => radioDelete(index)"/>
              </template>
            </a-input>
          </template>
        </a-form-item>
        <!-- ,  -->
        <a-form-item 
          v-if="selectedComponent.component.options.type === 'rate'"
          label=""
          class="attribute"
        >
          <a-input-number 
            v-model:value="selectedComponent.component.options.limit" 
            :min="1" 
            :max="20" 
          />
        </a-form-item>
      </template>
    </a-form>

  </a-layout>
</template>

<script setup>
import { ref, onMounted, computed } from "vue";
import axios from "axios";
import { useSelectedComponentStore } from "@/stores/component";
import { ExclamationCircleTwoTone, MinusCircleOutlined } from '@ant-design/icons-vue';

// 
const selectedComponent = useSelectedComponentStore();

// 
const typeDatas = ref([]);

// 
const schemas = ref([]);

// 
const fields = computed(() => {
  return typeDatas.value.find(val => val.schemaName === selectedComponent.component.schema)?.fields.map(val => {
    return {
      value: val.fieldName,
      label: val.fieldName
    };
  });
});

// 
function fieldClear() {
  selectedComponent.component.field = "";
}

// 
function radioAdd() {
  selectedComponent.component.options.valueData.push({value: ''});
}

// 
function radioDelete(index) {
  selectedComponent.component.options.valueData.splice(index, 1);
}

// 
onMounted(() => {
  axios.get('getType.do')
    .then((result) => {
      typeDatas.value = result.data.map(val => {
        val.fields = val.fields.map(field => ({
          "fieldName": field
        }));
        return {
          "schemaName": val.schemaName,
          "fields": val.fields
        };
      });
      schemas.value = typeDatas.value.map(val => {
        return {
          value: val.schemaName,
          label: val.schemaName
        };
      })
    })
    .catch((error) => {
      console.log(error);
    })
});
</script>

<style scoped>
.container {
  height: 100%;
  overflow-y: auto;
}

.title {
  height: 40px;
  color: black;
  font-size: 1rem;
  font-family:  Georgia, 'Times New Roman', Times, serif;
  display: flex;
  align-items: center;
  justify-content: center;
}

.divider {
  margin-top: 0;
  border-color: #1890ff;
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.attribute {
  width: 160px;
  margin: 10px;
}

input {
  border-radius: 5px;
}
</style>